<template>
  <div class="recommend">
    <HomeHeader :myBook="maleHot">{{ goback }}</HomeHeader>
    <BookList
      v-if="recommendListData"
      :fantasyData="recommendListData"
      :link="$route.query.id"
    />
  </div>
</template>


<script>
import HomeHeader from "@/components/HomeHeader.vue";
import BookList from "@/components/BookList.vue";
export default {
  props: [],
  data() {
    return {
      goback: "⮜ 返回",
      maleHot: this.$route.query.title,
      recommendListData: [],
    };
  },
  created() {
    this.axios
      .get("http://novel.kele8.cn/rank/" + this.$route.query.id)
      .then((result) => {
        console.log(this.$route.query.id);
        // console.log(this.$route.query.id);
        // console.log(result);
        this.recommendListData = result.data.ranking.books.slice(0, 20);
      });
  },
  components: {
    HomeHeader,
    BookList,
  },
};
</script>

